// SCSS for Card

.home-cards {
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
    
    // Item
    &__item {
        overflow: hidden;
        border-radius: 8px;

        // Hide all Cards except the First on Mobile
        &:not(:first-of-type) {
            display: none;
        }

        // Image
        &-image {
            display: block;
            width: 100%;
            height: 200px;
        }

        // Content
        &-content {
            padding: 30px;
            background-color: $color-white;
        }

        // Heading
        &-heading {
            margin: 0 0 20px 0;
            color: $color-black;
        }

        // Button
        &-button {
            display: flex;
            width: fit-content;
            align-items: center;
            gap: 8px;
            font-size: 15px;
            line-height: 18px;
            font-weight: 400;

            // Icon
            svg {
                display: block;
                width: 10px;
                height: auto;
            }

        }

    }

    @include bp-sm {

        & {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 30px;
            align-items: flex-start;
            
            // Item
            &__item {

                // Hide all Cards except the First and Second on Mobile
                &:not(:first-of-type) {
                    display: block;
                }
                
                &:not(:first-of-type, :nth-child(2)) {
                    display: none;
                }

            }

        }

    }

    @include bp-md {

        & {
            grid-template-columns: repeat(3, 1fr);

            // Used to describe the Opacity
            // that Cards should have when
            // they are not Hovered
            $opacity-when-not-hovered: 0.1;

            // Item
            &__item {
                transition: opacity 0.5s ease;

                // Show all Cards
                &:not(:first-of-type, :nth-child(2)) {
                    display: block;
                }

                // Make all the Cards opaque except the Second
                &:not(:nth-child(2)) {
                    opacity: $opacity-when-not-hovered;
                }

                // Image
                &-image {
                    height: 260px;
                }

                // Content
                &-content {
                    padding: 40px;
                }

                // [State] Hover
                &:hover {
                    opacity: 1 !important;
                }

            }

            // [State] Hover
            &:hover &__item {
                opacity: $opacity-when-not-hovered;
            }

        }

    }

}