// Reset

html,
body {
    padding: unset;
    margin: unset;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 40px;
}

body {
    text-rendering: optimizeLegibility;
    word-break: break-word;
}

::selection {
    background-color: $color-primary;
    color: $color-white;
}

*,
*::before,
*::after {
    font: inherit;
    box-sizing: border-box;
}

img,
svg {
    max-width: 100%;
    height: auto;
}

img {
    object-fit: cover;
    object-position: center;
    image-rendering: -webkit-optimize-contrast;
    
    // [State] Broken
    &::before {
        font-size: 14px;
        line-height: 24px;
        padding: 30px;
        background-color: $color-primary;
        color: $color-white;
    }

    &::before {
        content: attr(alt);
        display: block;
        width: 100%;
        height: 100%;
    }
    
}

a {

    img,
    svg {
        display: block;
    }

}

article,
section,
main,
aside,
blockquote,
cite,
hr {
    display: block;
}

button,
input,
textarea,
select {

    // [State] Disabled
    &:disabled {
        cursor: not-allowed;
    }

}