// SCSS for Hero

@keyframes mouseScrollingDown {

    from {
        top: 0;
        opacity: 0.7;
    }
    
    to {
        top: calc(100% - 2.5px);
        opacity: 0;
    }
    
}

// Hero
.home-hero {
    background-image: linear-gradient(to bottom, $color-primary-light -20%, $color-primary);
    padding: 100px 0 0 0;
    margin: 0 0 120px 0;
    
    // Content
    &__container {
        width: 80%;
        max-width: 620px;
        margin: 0 auto 80px auto;
        text-align: center;
    }

    // Heading
    &__heading {
        margin: 0 0 30px 0;
    }

    // Description
    &__description {
        margin: 0 auto 60px auto;
        max-width: 590px;
    }

    // Mouse
    &__mouse {
        margin: 0 auto;
        width: 25px;
        height: 40px;
        border: 1.5px solid $color-white;
        border-radius: 100px;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        padding: 10px 0;

        // Circle
        &-circle {
            position: relative;
            width: 5px;
            height: 5px;
            background-color: $color-pink;
            border-radius: 50%;
            animation: mouseScrollingDown 1.5s infinite ease;
        }

    }

    @include bp-md {

        & {

            // Container
            &__container {
                margin: 0 auto 90px auto;
            }

        }

    }

}