// SCSS for Trusted By

.home-trusted {
    margin: 0 0 120px 0;
    overflow-x: hidden;

    // Heading
    &__heading {
        width: 75%;
        text-align: center;
        margin: 0 auto 50px auto;
    }

    // Grid
    &__grid {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 0 0 -20px;
    }

    // Logo
    &__logo {
        height: fit-content;
        
        &,
        img {
            display: block;
            width: 100%;
            height: auto;
        }
        
        img {
            margin: 0 auto;
            object-fit: scale-down;
        }

    }

    @include bp-md {

        & {
            margin: 0 0 180px 0;

            // Heading
            &__heading {
                margin: 0 auto 60px auto;
            }

            // Grid
            &__grid {
                transform: unset !important;
                width: 80%;
                max-width: 1200px;
                margin: 0 auto;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: center;
                gap: 60px;
            }

            // Logo
            &__logo {
                width: auto;
                height: fit-content;

                // Image
                img {
                    width: auto;
                    height: 50px;
                }

            }

        }

    }

}