// Paragraphs

body,
p {
    font-family: $font-primary;
    font-size: 18px;
    line-height: 32px;
    font-weight: 300;
}

body {
    background-color: $color-primary;
}

p {
    color: $color-pink;
    opacity: 0.7;
}

// Headings

h1,
h2,
h3,
h4,
h5,
h6 {
    color: $color-white;
    margin: 0 0 30px 0;
}

h1,
h2,
h4,
h5,
h6 {
    font-weight: 400;
}

h1 {
    font-size: 25px;
    line-height: 40px;
    letter-spacing: -0.5px;

    @include bp-md {

        & {
            font-size: 40px;
            line-height: 55px;
            letter-spacing: -0.8px;
        }

    }

}

h2 {
    font-size: 23px;
    line-height: 35px;
    letter-spacing: -0.46px;

    @include bp-md {

        & {
            font-size: 30px;
            line-height: 45px;
            letter-spacing: -0.6px;
        }

    }

}

h3 {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: -0.4px;

    @include bp-md {

        & {
            font-size: 25px;
            line-height: 35px;
            letter-spacing: -0.5px;
        }

    }

}

// Links

a {
    text-decoration: none;
    color: $color-blue;
}