// SCSS for Features

.home-features {
    width: 80%;
    max-width: 1050px;
    margin: 0 auto 100px auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 100px;

    // Item
    &__item {
        width: 100%;
        max-width: 500px;

        // Image
        &-image {
            display: block;
            width: 100%;
            height: auto;
            margin: 0 0 30px 0;
            border-radius: 8px;
        }

        // Heading
        &-heading {
            margin: 0 0 25px 0;
        }

        // Description
        &-description {
            margin: unset;
        }

    }

    @include bp-md {

        & {
            margin: 0 auto 120px auto;

            // Item
            &__item {
                max-width: unset;
                display: grid;
                grid-template-columns: auto 460px;
                column-gap: 100px;
                align-items: center;

                &-image,
                &-content {
                    grid-row-start: 1;
                }

                // Image
                &-image {
                    margin: unset;
                    grid-column-start: 2;
                }

                // Content
                &-content {
                    grid-column-start: 1;
                }

                // [Modifier] Reverse
                &:nth-child(even) {
                    grid-template-columns: 460px auto;
                }

                &:nth-child(even) &-image {
                    grid-column-start: 1;
                }

                &:nth-child(even) &-content {
                    grid-column-start: 2;
                }

            }

        }

    }

    @media only screen and (min-width: 1200px) {

        & {
            
            // Item
            &__item {
                grid-template-columns: auto 510px;

                // [Modifier] Reverse
                &:nth-child(even) {
                    grid-template-columns: 510px auto;
                }

            }

        }

    }

}