// SCSS for Button

@mixin button {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 10px;
    width: fit-content;
    border-radius: 8px;
    padding: 22px 40px;
    font-size: 18px;
    line-height: 22px;
    font-weight: 500;
    text-decoration: none;
    backface-visibility: hidden;
    transition: 0.3s ease;

    // Icon
    svg {
        display: block;
        width: 14px;
        height: auto;
    }

    // [State] Hover
    &:hover {
        transform: translateY(-3px);
    }

}

// Add this class to your Buttons in addition to another more
// specific class. Example: <a class="button button-primary"> ... </a>
.button {
    @include button;
}

// Primary
.button-primary {
    background-color: $color-white;
    color: $color-primary;
}

// Secondary
.button-secondary {
    background-color: transparent;
    border: 1px solid $color-gray-dark;
    color: $color-white;

    // [State] Hover
    &:hover {
        border-color: $color-white;
    }

}