// SCSS for Home Buttons

.home-buttons {
    gap: 20px;
    width: 80%;
    margin: 0 auto 40px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    // Button
    .button {
        width: 100%;
    }

    @include bp-sm {

        & {
            flex-direction: row;
            margin: 0 auto 120px auto;

            // Button
            .button {
                width: fit-content;
            }

        }

    }

}